/** @jsx jsx */
import { css, jsx } from "@emotion/core";
import { Fragment } from "react";

import { FormState } from "../../globals.d";

import TextLink from "../../components/TextLink";
import InfoBloc from "../../components/InfoBloc";
import RecapBloc from "./components/RecapBloc";

interface Props {
  indicateurQuatreFormValidated: FormState;
  indicateurQuatreCalculable: boolean;
  indicateurEcartNombreSalarieesAugmentees: number | undefined;
  noteIndicateurQuatre: number | undefined;
}

function RecapitulatifIndicateurQuatre({
  indicateurQuatreFormValidated,
  indicateurQuatreCalculable,
  indicateurEcartNombreSalarieesAugmentees,
  noteIndicateurQuatre
}: Props) {
  if (indicateurQuatreFormValidated !== "Valid") {
    return (
      <div css={styles.container}>
        <InfoBloc
          title="Indicateur 4, pourcentage de salaries augmentes dans l'anne suivant leur retour de cong maternit"
          text={
            <Fragment>
              <span>
                Nous ne pouvons pas calculer votre indicateur car vous navez
                pas encore valid vos donnes saissies.
              </span>{" "}
              <TextLink to="/indicateur4" label="valider les donnes" />
            </Fragment>
          }
        />
      </div>
    );
  }

  if (!indicateurQuatreCalculable) {
    return (
      <div css={styles.container}>
        <InfoBloc
          title="Indicateur 4, pourcentage de salaries augmentes dans l'anne suivant leur retour de cong maternit"
          text="Malheureusement votre indicateur nest pas calculable  car il ny a pas eu de cong maternit durant la priode de rfrence"
        />
      </div>
    );
  }

  return (
    <div css={styles.container}>
      <RecapBloc
        title="Indicateur 4, pourcentage de salaries augmentes dans l'anne suivant leur retour de cong maternit"
        resultBubble={{
          firstLineLabel: "votre rsultat final est",
          firstLineData:
            (indicateurEcartNombreSalarieesAugmentees !== undefined
              ? indicateurEcartNombreSalarieesAugmentees.toFixed(1)
              : "--") + " %",
          secondLineLabel: "votre note obtenue est",
          secondLineData:
            (noteIndicateurQuatre !== undefined ? noteIndicateurQuatre : "--") +
            "/15",
          indicateurSexeSurRepresente: "femmes"
        }}
      >
        {null}
      </RecapBloc>
    </div>
  );
}

const styles = {
  container: css({
    display: "flex",
    flexDirection: "column",
    marginTop: 22,
    marginBottom: 22
  })
};

export default RecapitulatifIndicateurQuatre;
