/** @jsx jsx */
import { css, jsx } from "@emotion/core";
import { Fragment } from "react";

import { FormState } from "../../globals.d";

import TextLink from "../../components/TextLink";
import InfoBloc from "../../components/InfoBloc";
import RecapBloc from "./components/RecapBloc";

interface Props {
  indicateurCinqFormValidated: FormState;
  indicateurSexeSousRepresente: "hommes" | "femmes" | "egalite" | undefined;
  indicateurNombreSalariesSexeSousRepresente: number | undefined;
  noteIndicateurCinq: number | undefined;
}

function RecapitulatifIndicateurCinq({
  indicateurCinqFormValidated,
  indicateurSexeSousRepresente,
  indicateurNombreSalariesSexeSousRepresente,
  noteIndicateurCinq
}: Props) {
  if (indicateurCinqFormValidated !== "Valid") {
    return (
      <div css={styles.container}>
        <InfoBloc
          title="Indicateur 5, nombre de salaris du sexe sous-reprsent parmi les 10 plus hautes rmunrations"
          text={
            <Fragment>
              <span>
                Nous ne pouvons pas calculer votre indicateur car vous navez
                pas encore valid vos donnes saissies.
              </span>{" "}
              <TextLink to="/indicateur1" label="valider les donnes" />
            </Fragment>
          }
        />
      </div>
    );
  }

  const firstLineInfo =
    indicateurSexeSousRepresente === undefined
      ? undefined
      : indicateurSexeSousRepresente === "egalite"
      ? "les hommes et les femmes sont  parit"
      : indicateurSexeSousRepresente === "hommes"
      ? "les femmes sont sur-reprsentes"
      : "les hommes sont sur-reprsents";

  return (
    <div css={styles.container}>
      <RecapBloc
        title="Indicateur 5, nombre de salaris du sexe sous-reprsent parmi les 10 plus hautes rmunrations"
        resultBubble={{
          firstLineLabel: "votre rsultat final est",
          firstLineData:
            indicateurNombreSalariesSexeSousRepresente !== undefined
              ? String(indicateurNombreSalariesSexeSousRepresente)
              : "--",
          firstLineInfo,
          secondLineLabel: "votre note obtenue est",
          secondLineData:
            (noteIndicateurCinq !== undefined ? noteIndicateurCinq : "--") +
            "/10",
          indicateurSexeSurRepresente:
            indicateurSexeSousRepresente === undefined
              ? undefined
              : indicateurSexeSousRepresente === "hommes"
              ? "femmes"
              : "hommes"
        }}
      >
        {null}
      </RecapBloc>
    </div>
  );
}

const styles = {
  container: css({
    display: "flex",
    flexDirection: "column",
    marginTop: 22,
    marginBottom: 22
  })
};

export default RecapitulatifIndicateurCinq;
